class Choose extends React.Component {
renderChoose(i) {
    return (
      <button className="choose" 
	    currentchoose={this.props.currentchoose}
	    onClick={() => this.props.onClick(i)}>
		{i}
	  </button>
    );
  }

  render() {
    return (
      <div>
        <div className="chooseBox">
          {this.renderChoose('Brot')}
          {this.renderChoose('Brötchen')}
          {this.renderChoose('Süß')}
          {this.renderChoose('Hand')}
        </div>
      </div>
    );
  }
}

