ReactDOM.render(<Game />, document.getElementById("root"));


function calculateCount(counts, types, type) {
	let count = 0;
	let i = 0;
	for (let value of types) {
		if (value === type) {
		count = count + counts[i];
			// alert('types_:' + types[i] + ' type:' + type + ' count:' + count);

		}
	i++;
	}
	return count;
}


function calculateSum(counts1, prices1) {
	let sum = 0;
	let i = 0;
	for (let value of counts1) {
		sum = sum + ( value * prices1[i] );
		i++;
	}
	return sum.toLocaleString('de-DE', { style: 'currency', currency: 'EUR' });
}